.demo-big-content{
background-color: #dde1e7;

}

.header-color{
  background: #808080;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #3fada8, #808080);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #3fada8, #808080); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.landing-grid{
  background-color: #dde1e7;
  
}

.container{
  background-color: #dde1e7;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;


}


.neu{
  margin: 30px;
  height:200px;
  width: 200px;
  background: #dde1e7;
  display: flex;
  justify-content: center;
  box-shadow: inset 3px 3px 15px #ffffff , -3px -3px 15px #000000; 
  border-radius: 20%;

}

.neu img {
  padding: 10%;
  border-radius: 25%;
 
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin:auto;
  border-radius: 30px;
  margin-top: 20px;
 text-align: center;
}

.banner-text h1{
  font-size: 40px;

  background: -webkit-linear-gradient( #3fada8, #808080 );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

  font-size:56px;
  color: #3fada8;
  text-align: center;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;

}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.Social-Links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
  align-content: center;
  justify-content: center;
  flex-direction: inherit;

}

.Social-Links i{
  color: white;
  font-size: 3.5em;
  padding: 8px;
 
}

.projects-grid{
  display: flex;
  margin: auto;
  padding: 10px;
  flex-direction: column;
 
}
.card-css{
 margin: auto;
  padding: 10px;
  background: black;
}

/* contact-page css*/
.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  color: #000000;
  overflow: scroll;

}
.contact-body a{
  color: #000000;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width:80%;
  height: 555px;
  background: white;
  color: black;
     
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;

}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;

}

.contact-grid hr{
  border-top: 5px dotted black;
  width:50%;
  margin:auto;
  align-content: center;
}
.contact-list i{
    font-size: 46px;
    padding-right: 1rem;

  }



/* contact-page css*/




